        }\r\n                }\r\n\r\n                .input-group {\r\n                    display: flex;\r\n                    flex-direction: column;\r\n                    gap: 8px;\r\n\r\n                    label {\r\n                        @include text;\r\n                        color: #333;\r\n                    }\r\n\r\n                    .form-input {\r\n                        @include text;\r\n                        padding: 12px 15px;\r\n                        border: 1px solid #ddd;\r\n                        border-radius: 8px;\r\n                        transition: border-color 0.3s;\r\n\r\n                        &:focus {\r\n                            border-color: #70CAF3;\r\n                            outline: none;\r\n                        }\r\n\r\n                        &.textarea {\r\n                            resize: vertical;\r\n                            min-height: 100px;\r\n                        }\r\n                    }\r\n                }\r\n\r\n                .form-notice {\r\n                    @include text;\r\n                    font-size: 0.9em;\r\n                    color: #666;\r\n                    text-align: center;\r\n                    margin-top: 15px;\r\n                    opacity: 80%;\r\n                }\r\n                \r\n                .form-submit-wrapper {\r\n                    display: flex;\r\n                    align-items: center;\r\n                    gap: 20px;\r\n                    margin-top: 20px;\r\n                }\r\n            \r\n                .success-message {\r\n                    @include text;\r\n                    color: #28a745;\r\n                    padding: 10px 15px;\r\n                    background: #d4edda;\r\n                    border-radius: 5px;\r\n                    border: 1px solid #c3e6cb;\r\n                    display: none; // Скрыто по умолчанию\r\n                    animation: fadeIn 0.3s ease;\r\n                    @include fade-t